/* Swipers */

const jobSwiper = new Swiper(".job__swiper", {
	slidesPerView: 1.1,
	spaceBetween: 10,
	breakpoints: {
		1200: {
			slidesPerView: 4,
			spaceBetween: 20,
		},
		1000: {
			slidesPerView: 3.1,
			spaceBetween: 16,
		},
		580: {
			slidesPerView: 2.1,
			spaceBetween: 12,
		},
	},
});

const awardsSwiper = new Swiper(".awards__swiper", {
	slidesPerView: 1.1,
	spaceBetween: 10,
	navigation: {
		nextEl: ".awards__swiper-button-next",
		prevEl: ".awards__swiper-button-prev",
	},
	breakpoints: {
		1500: {
			slidesPerView: 4,
			spaceBetween: 20,
		},
		1200: {
			slidesPerView: 3.1,
			spaceBetween: 16,
		},
		580: {
			slidesPerView: 2.1,
			spaceBetween: 12,
		},
	},
});

const teamSwiper = new Swiper(".team__swiper", {
	slidesPerView: 1,
	spaceBetween: 10,
	pagination: {
		el: ".team__swiper-pagination",
	},
});

const storiesSwiper = new Swiper(".stories__swiper", {
	slidesPerView: 1.1,
	spaceBetween: 10,
	centeredSlides: false,
	//loop: true,
	navigation: {
		nextEl: ".stories__swiper-button-next",
		prevEl: ".stories__swiper-button-prev",
	},
	breakpoints: {
		1024: {
			slidesPerView: 1.1,
			spaceBetween: 30,
			centeredSlides: true,
		},
	},
});

const positionSwiper = new Swiper(".position__swiper", {
	slidesPerView: 1.1,
	spaceBetween: 10,
	breakpoints: {
		1024: {
			slidesPerView: 3,
			spaceBetween: 20,
		},
		600: {
			slidesPerView: 2.1,
			spaceBetween: 16,
		},
	},
});

const intro4Swiper = new Swiper(".intro4Swiper", {
	slidesPerView: 1,
	spaceBetween: 10,
	navigation: {
		nextEl: ".intro__swiper-button-next",
		prevEl: ".intro__swiper-button-prev",
	},
	pagination: {
		el: ".intro__swiper-pagination",
		type: "fraction",
	},
	scrollbar: {
		el: ".intro__swiper-scrollbar",
	},
	breakpoints: {
		920: {
			slidesPerView: 3,
			spaceBetween: 20,
		},
		550: {
			slidesPerView: 2,
			spaceBetween: 10,
		},
	},
});

// services
const servicesCardTitle = document.querySelectorAll(".services__card_title");
const services4Swiper = new Swiper(".services4Swiper", {
	slidesPerView: 1.1,
	spaceBetween: 0,
	pagination: {
		el: ".services__swiper-pagination",
		clickable: true,
		renderBullet: function (index, className) {
			return '<span class="' + className + '">' + (servicesCardTitle[index] ? servicesCardTitle[index].textContent : "") + "</span>";
		},
	},
	navigation: {
		nextEl: ".services__swiper-button-next",
		prevEl: ".services__swiper-button-prev",
	},
	breakpoints: {
		1240: {
			spaceBetween: 25,
		},
	},
});
if (window.innerWidth > 1240) {
	services4Swiper.enable();
} else {
	services4Swiper.disable();
}

/* End swipers */
